import type {
  AnyObj,
  IStudentExams,
  IListExamResult,
  IExamResult,
  IExamComment,
  IQuestionComment,
  Loading,
} from '@smarttestpro/smart-common';
import {create, Api, ApiClient} from '@smarttestpro/smart-common';

const SmartTestClient = ApiClient.getInstance('smart-test');
SmartTestClient.setEndpoint('https://api.smarttest.pro/api');

type ExamResultStore = {
  listStudentExams: IStudentExams[];
  listStudentExamsWait: IStudentExams[];
  listExamResult: IListExamResult[];
  examResultDetail: IExamResult;
  examComment: IExamComment;
  loading: Loading;
  saveLoading: Loading;
};

type ExamResultStoreAction = {
  getListStudentExams: (maThiSinh: string) => void;
  getListStudentExamsWait: (maThiSinh: string) => void;
  getListExamResult: (maThiSinh: string, maDethi: string) => void;
  getExamResultDetail: (maKiemTra: string) => void;
  commentStudentExam: (data: IExamComment) => void;
  commentQuestion: (data: IQuestionComment) => void;
  getExamComment: (maKiemTra: string) => void;
};

const initValue: ExamResultStore = {
  listStudentExams: [],
  listStudentExamsWait: [],
  listExamResult: [],
  examResultDetail: {} as AnyObj,
  examComment: {} as AnyObj,
  loading: 'none',
  saveLoading: 'none',
};

export const useExamResultStore = create<
  ExamResultStore & ExamResultStoreAction
>()((set, _get) => ({
  ...initValue,
  getListStudentExams: async (maThiSinh: string) => {
    const result = await Api.ExamSmart.getStudentExam(maThiSinh);
    set({listStudentExams: result.data});
  },
  getListStudentExamsWait: async (maThiSinh: string) => {
    const result = await Api.ExamSmart.getStudentExamWait(maThiSinh);
    set({listStudentExamsWait: result.data});
  },
  getListExamResult: async (maThiSinh: string, maDethi: string) => {
    const result = await Api.ExamSmart.getStudentExamResult(maThiSinh, maDethi);
    set({listExamResult: result.data});
  },
  getExamResultDetail: async (makiemtra: string) => {
    const result = await Api.ExamSmart.getStudentExamResultDetail(makiemtra);
    set({examResultDetail: result.data});
  },
  commentQuestion: async (data: IQuestionComment) => {
    try {
      await Api.ExamSmart.commentQuestion(data);
      set({saveLoading: 'success'});
      // notificationApi.success({
      //   message: _t('notification.success'),
      //   description: _t('notification.success'),
      // });
      return true;
    } catch (error) {
      console.log(error);
      // notificationApi.success({
      //   message: _t('notification.error'),
      //   description: _t('notification.somethingWentWrong'),
      // });
      set({saveLoading: 'error'});
      return false;
    }
  },
  commentStudentExam: async (data: IExamComment) => {
    try {
      await Api.ExamSmart.commentExam(data);
      set({saveLoading: 'success'});
      // notificationApi.success({
      //   message: _t('notification.success'),
      //   description: _t('notification.success'),
      // });
      return true;
    } catch (error) {
      console.log(error);
      // notificationApi.success({
      //   message: _t('notification.error'),
      //   description: _t('notification.somethingWentWrong'),
      // });
      set({saveLoading: 'error'});
      return false;
    }
  },
  getExamComment: async (maKiemTra: string) => {
    try {
      const result = await Api.ExamSmart.getExamComment(maKiemTra);
      set({examComment: result.data});
    } catch (error) {
      console.log(error);
      set({examComment: {} as AnyObj});
    }
  },
}));
